/* global google */
import React from "react";
import {
  withGoogleMap,
  GoogleMap,
  withScriptjs,
  Marker,
  DirectionsRenderer
} from "react-google-maps";

class MapDirectionsRenderer extends React.Component {
  state = {
    directions: null,
    error: null
  };

  componentDidMount() {
    const { origin, dest } = this.props;
    
    // const waypoints = origins.map(p =>({
    //     location: {lat: p.latitude, lng:p.longitude},
    //     stopover: true
    // }))
    console.log("directions renderer props", this.props)
    console.log("directions renderer origin + dest", origin, dest)
    //const origin = origin[0];
    const destination = dest;
    
    

    const directionsService = new google.maps.DirectionsService();
    directionsService.route(
      {
        origin: origin,
        destination: destination,
        travelMode: google.maps.TravelMode.DRIVING,
        //waypoints: waypoints
      },
      (result, status) => {
        if (status === google.maps.DirectionsStatus.OK) {
          this.setState({
            directions: result,
            suppressMarkers: true
          });
        } else {
          console.error(`error fetching directions ${result}`);
          this.setState({ error: result });
        }
        var leg = result.routes[ 0 ].legs[ 0 ];
        makeMarker( leg.start_location, icons.start, "title" );
        makeMarker( leg.end_location, icons.end, 'title' );

      }
    );
  }

  render() {
    if (this.state.error) {
      return <h1>{this.state.error}</h1>;
    }
    return (this.state.directions && <DirectionsRenderer directions={this.state.directions} />)
  }
}

//custom markers -------------------------

function makeMarker( position, icon, title ) {
  new google.maps.Marker({
   position: position,
  //  map: map,
   icon: icon,
   title: title
  });
 }
 
  var icons = {
   start: new google.maps.MarkerImage(
    // URL
    'start.png',
    // (width,height)
    new google.maps.Size( 44, 32 ),
    // The origin point (x,y)
    new google.maps.Point( 0, 0 ),
    // The anchor point (x,y)
    new google.maps.Point( 22, 32 )
   ),
   end: new google.maps.MarkerImage(
    // URL
    'end.png',
    // (width,height)
    new google.maps.Size( 44, 32 ),
    // The origin point (x,y)
    new google.maps.Point( 0, 0 ),
    // The anchor point (x,y)
    new google.maps.Point( 22, 32 )
   )
  };

